@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  box-sizing: border-box;
}

html,
body,
#root,
.app {
  height: 100%;
}

body {
  margin: 0;
  font-family: "Roboto";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: grid;
  grid-template-columns: 117px auto;
  grid-template-rows: 91px 1fr 250px;

  grid-template-areas:
    "header header"
    "aside main"
    "footer main";
}

.header{grid-area:header;color:white;background:#020203;box-shadow:0px 4px 4px rgba(0,0,0,0.25);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.header__logo{height:58px;margin-left:29px}.nav{width:100%}.nav ul{padding:0;list-style-type:none;display:-webkit-flex;display:flex;font-weight:500;font-size:24px;line-height:24px;-webkit-justify-content:space-around;justify-content:space-around}.nav ul li{display:inline-block}

.greeting{grid-area:greeting}.greeting__text{font-weight:500;margin:0}.greeting__text--name{color:#e60000}.greeting__status{font-size:18px;line-height:24px;margin-top:40px}@media (max-width: 1200px){.greeting__status{margin-top:20px}}

.dailyActivity{grid-area:daily;min-width:300px;height:320px;padding:24px 32px;position:relative}@media (max-width: 1200px){.dailyActivity{padding-top:0;height:276px}}.dailyActivity h2{font-family:Roboto;font-style:normal;font-weight:500;font-size:15px;line-height:24px;margin:0;position:absolute;top:29px}@media (max-width: 1200px){.dailyActivity h2{top:5px}}


.barChart__tooltip{font-family:Roboto;font-style:normal;font-weight:500;font-size:9px;line-height:24px;text-align:center;color:#ffffff;width:43px;height:63px;background-color:#e60000;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center}.barChart__tooltip p{margin:0}

.lineChart__tooltip{font-family:Roboto;font-style:normal;font-weight:500;font-size:7px;line-height:24px;text-align:center;color:black;width:39px;height:25px;background-color:#ffffff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center}.lineChart__tooltip p{margin:0}

.charts{grid-area:charts;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin-top:30px;margin-bottom:70px}@media (max-width: 1200px){.charts{margin-top:20px;margin-bottom:30px}}.chart{-webkit-flex-basis:32%;flex-basis:32%;height:263px;position:relative}@media (max-width: 1200px){.chart{-webkit-flex-basis:48%;flex-basis:48%;height:180px}}.radialBarChart__headline{margin:0;position:absolute;top:10%;left:10%;font-family:Roboto;font-style:normal;font-weight:500;font-size:15px;line-height:24px}.lineChart__headline{position:absolute;margin:0;top:25px;left:30px;z-index:10;color:white;opacity:0.5;font-family:Roboto;font-style:normal;font-weight:500;font-size:15px;line-height:24px}

.card{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-top:32px;padding-left:32px;padding-bottom:32px;height:124px;width:258px}@media (max-width: 1200px){.card{height:62px}}.card__image{width:60px;height:60px;margin-right:24px}@media (max-width: 1200px){.card__image{width:50px;height:50px}}.card__text--stat{font-weight:700;font-size:20px;line-height:24px;margin:0}@media (max-width: 1200px){.card__text--stat{font-size:18px;line-height:22px}}.card__text--cat{font-weight:500;font-size:14px;line-height:24px;color:#74798c;margin:2px 0 0 0}@media (max-width: 1200px){.card__text--cat{font-size:12px;line-height:22px}}

.cards{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;margin-left:10px;grid-area:cards;margin-bottom:70px}@media (max-width: 1200px){.cards{margin-bottom:0;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}}

.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-area:main;padding-top:68px;padding-left:107px}@media (max-width: 1200px){.main{padding-top:48px;padding-left:77px}}.main__content{width:100%;display:grid;grid-template-columns:1fr -webkit-fit-content;grid-template-columns:1fr fit-content;grid-template-rows:auto auto;grid-template-areas:"daily cards" "charts cards"}@media (max-width: 1200px){.main__content{grid-template-areas:"daily cards" "charts charts"}}

.aside{grid-area:aside;background:#020203;color:white;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around}.aside div{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.aside__icon{width:64px;height:64px}.aside__icon+.aside__icon{margin-top:20px}

.footer{grid-area:footer;background:#020203;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}.footer__content{-webkit-transform:rotate(-90deg);transform:rotate(-90deg);white-space:nowrap;font-size:12px;line-height:24px;color:white}

